<template>
  <div class="contents">
    <div class="form-wrapper form-wrapper-sm">
      <div class="form">
        <input
          type="text"
          placeholder="보낼 메세지를 입력하세요"
          v-model="msg"
          @keyup.13="submitMessageFunc"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: '',
    };
  },
  methods: {
    submitMessageFunc() {
      if (this.msg.length === 0) return false;
      this.$emit('submitMessage', this.msg);
      this.msg = '';
      return true;
    },
  },
};
</script>

