<template>
  <div>
    <h1 class="page-header">Chat bot</h1>
    <ChatBotList :msgs="msgList" />
    <ChatBotForm @submitMessage="sendMsg" />
  </div>
</template>

<script>
// import { mapMutations, mapState } from 'vuex';
import ChatBotForm from '@/components/chat/ChatBotForm.vue';
import ChatBotList from '@/components/chat/ChatBotList.vue';

export default {
  data() {
    return {
      msg: '',
      msgList: [],
      ws: '',
    };
  },
  components: {
    ChatBotForm,
    ChatBotList,
  },
  created() {
    this.ws = new WebSocket(
      'ws://ec2-18-216-25-180.us-east-2.compute.amazonaws.com:3000',
    );
    this.ws.onopen = () => {
      this.ws.send('접속');
    };
  },
  methods: {
    sendMsg(data) {
      this.ws.send(data);
      this.ws.onmessage = ({ data }) => {
        this.msgList.push(data);
      };
    },
  },
};
</script>

